canvas:focus {
  outline: none
}

div {
  white-space: pre-wrap;
} 

.tippy-box[data-theme~='tomato'] {
  background-color: rgba(247, 196, 66, 0.65);
  color: #080808;
  border-radius: 0;
  line-height: 1.6;
  padding: 10px
}

.tippy-box[data-theme~='tomato'][data-placement^='top'] > .tippy-arrow::before {
  border-top-color: rgba(247, 196, 66, 0);
}

.index-text {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff
}

.index-header {
  font-size: 48px;
  color: #080808;
  font-weight: 600;
  letter-spacing: 0.05em;
}
.index-body {
  font-size: 14px;
  font-weight: 200;
  padding-top: 1em;
  line-height: 2.1;
  font-family: sans-serif; 
  color: #888888
}

.index-link {
  font-size: 16px;
  font-weight: 100;
  font-family: 'Merriweather Sans', sans-serif;
  color: #080808
}

