.App {
  height: 100vh;
  width: 100vw;
  background-color: #080808;
  font-family: 'Merriweather Sans', sans-serif;
}

.html, body, #root, #root>div {
  height: 100%;
  margin: 0;
  color: #080808;
}

/* unvisited link */
a:link {
  color: #080808;
  text-decoration: none;
}

/* visited link */
a:visited {
  color: #080808;
  text-decoration: none;
}

/* mouse over link */
a:hover {
  color: #F7C242;
  text-decoration: none;
}

/* selected link */
a:active {
  color: #F7C242;
  text-decoration: none;
}